.transactionHistory {
    text-align: center;
  
    width: 600px;
    margin: 0 auto;;
  }
  
  
  th {
    font-size: 13px;
    font-weight: normal;
    background: #b9c9fe;
    border-top: 4px solid #aabcfe;
    border-bottom: 1px solid white;
    color: #039;
    padding: 8px;
  }
  td {
    background: #e8edff;
    border-bottom: 1px solid white;
    color: #669;
    border-top: 1px solid transparent;
    padding: 8px;
  }
  tr:hover td {
    background: #ccddff;
  }

  